import {createRouter,createWebHistory} from "vue-router";
import {pages} from './dynamic.config.js'
import Root from './render.vue'
const routes = [
  {
    path: "/:name",
    name: "routes",
    component:Root,
    beforeEnter: (to, from, next) => {
      if (to.params.name in pages) {
        next();
      } else {
        next('/');
      }
    }
  },
  {
    path: '/',
    component:Root,
    name:'r',
    beforeEnter: (to, from, next) => {
      next('/investment');
    }
  }
];

const router =createRouter({
  history: createWebHistory("/dynamic.html"),
  routes,
});

export default router;
