import { createApp } from 'vue'
import router from './router'
import dynamic from './dynamic.vue'
import axios from 'axios'
import regFunc from '../index/regCustomElement'
import {
    message,
} from "ant-design-vue";
regFunc();
const app = createApp(dynamic).use(router)
app.config.warnHandler = () => null;
app.provide("axios", axios)
    .mixin({ inject: ["axios", "goto", "reactive"] }).use(message)
app.config.compilerOptions.isCustomElement = tag => tag.startsWith('go-') || tag.startsWith('xy') || tag.startsWith('common') || tag.startsWith('phone') || tag.startsWith('window');
if (document.querySelector("#dynamic").childNodes.length == 0) {
    app.mount("#dynamic");
} else {
    app.mount("#dynamic",false)
}
